.intro {
	font-family: "Advent Pro", sans-serif;
	color: white;
	font-size: 1.5rem;
	padding: 10rem 0.5rem 4rem;
	position: relative;
	width: 90%;
	margin: 0 auto;
}

.apps {
	position: relative;
	display: flex;
	flex-direction: row;
	justify-content: space-around;
	width: 100%;
	flex-wrap: wrap;
}

@media (max-width: 950px) {
	.intro {
		font-size: 1.2rem;
	}
}

@media (max-width: 700px) {
	.back .panel-description {
		font-size: 1rem;
		font-weight: 700;
		color: rgb(4, 26, 32);
		line-height: 1.2rem;
		overflow-y: hidden;
	}
}

@media (max-width: 600px) {
	.intro {
		font-size: 1rem;
	}
}
